import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { EXCLUDEUNITS, INCLUDEUNITS, IMAGESPORTRAITS } from '../Constants'
import { checkContainsInList, extractHeroName } from '../Utils'

class HeroPortraits extends Component {

  loadPortraits () {
    var heroes = []
    var i = 0
    for (let image in IMAGESPORTRAITS) {
      i += 1
      heroes.push(this.buildPortraits(image, i))
    }
    return (heroes)
  }

  buildPortraits (image, idx) {
    var heroKey = extractHeroName(image)
    if (!checkContainsInList(EXCLUDEUNITS, heroKey) || checkContainsInList(INCLUDEUNITS, heroKey)) {
      return (
          <div key={'div:' + idx} className='hero'>
            <div key={'div:' + idx + ':container'} className='hero-portrait-container'>
              <Link key={'/hero/'+ heroKey} to ={'/hero/'+ heroKey}>
              <img key={idx} id = {heroKey} className="hero-portrait" src={IMAGESPORTRAITS[image]} alt = '' />
              </Link>  
            </div>
            <span key={'tooltip:' + heroKey} className='hero-tooltip'>{heroKey}</span>
          </div>
      )
    }
  }

  render () {
    return (
      <div className="heroes">
        {this.loadPortraits()}
      </div>
    )
  }
}

export default HeroPortraits


